<template>
  <risk-form v-if="ready">

  </risk-form>
</template>

<script>
import RiskForm from '@/components/RiskForm'
import riskTypes from '@/api'

export default {
  components: { RiskForm },
  data () {
    return {
      ready: false,
      riskType: null
    }
  },
  async mounted () {
    this.riskType = await riskTypes.get(1)
    this.ready = true
  }
}
</script>

<style>

</style>



